<template>
  <div class="look-text-item" >
    <div class="container-look-text-item">
        <div class="look-text-item-top_all">
          <div class="back" @click.stop="back()">
          <img src="../../assets/image/联合42.png" alt="">
        </div>
            <div class="look-text-item-top-header">
                    回顾试卷
            </div>
            <router-view></router-view>
        </div>
      <!--题目内容-->
      <div class="swiper-container  body ">
        <div class="swiper-wrapper " >
          <div  v-for="(item, index) in allData" :key="index" class="swiper-slide">
            <div class="fontstyle">
              <div v-if="item.typeCode == '0101'" class="typePhoto">单选题</div>
              <div v-if="item.typeCode == '0102'" class="typePhoto">多选题</div>
              <div v-if="item.typeCode == '0103'" class="typePhoto">判断题</div>
              <div v-if="item.typeCode == '0104'" class="typePhoto">不定项</div>
              <div class="text_mian_title"> &nbsp;&nbsp;{{pageIndex}}.{{item.stem}}</div>
            </div>
            <div v-for="(i,index) in item.exeOptionList" :key="index" class="swiper-slide options" >
              <div class="text-main-options" >
                <div v-if="i.isTrue == '0' && !i.selected" style="width: 0.42rem;height: 0.42rem;background-size:100% 100%;" :class="String.fromCharCode(65+index)" :id="'1_'+i.exerciseId+'_'+String.fromCharCode(65 + index)"></div>
                <div v-if="i.isTrue == '0' && i.selected" style="width: 0.42rem;height: 0.42rem;background-size:100% 100%;" :class="String.fromCharCode(65+index)+'-eor'" :id="'1_'+i.exerciseId+'_'+String.fromCharCode(65 + index)"></div>
                <div v-if="i.isTrue == '1'" style="width: 0.42rem;height: 0.42rem;background-size:100% 100%;" :class="String.fromCharCode(65+index)+'-get'" :id="'1_'+i.exerciseId+'_'+String.fromCharCode(65 + index)"></div>&nbsp;&nbsp;&nbsp;&nbsp;
                <div style="width: 88%;font-size:0.36rem;line-height: 0.48rem;">
                  <span v-if="i.isTrue == '0' && !i.selected" :id=" '1_' +  String.fromCharCode(65 + index) + '_' + i.exerciseId ">{{i.options}}</span>
                  <span v-if="i.isTrue == '0' && i.selected" :id=" '1_' +  String.fromCharCode(65 + index) + '_' + i.exerciseId " style="color:red;">{{i.options}}</span>
                  <span v-if="i.isTrue == '1' && i.selected" :id=" '1_' +  String.fromCharCode(65 + index) + '_' + i.exerciseId " style="color:#04cd9d;">{{i.options}}</span>
                  <span v-if="i.isTrue == '1' && !i.selected" :id=" '1_' +  String.fromCharCode(65 + index) + '_' + i.exerciseId " >{{i.options}}</span>
                </div>
              </div>
            </div>

            <div >
              <div class="balckMess"></div>
              <div style='padding:0 0.35rem'>
                <div style="color:#333333;font-size:0.32rem;margin-top: 0.35rem;">试题解析</div>
                <div style="margin-top: 0.17rem;margin-bottom: 0.29rem;">
                  <div class="balckMess-dn">
                    答案：{{item.ansString}}
                  </div>
                  <div class="balckMess-cont">
                    {{item.exeAnswer.wordAnswer}}
                  </div>
                  <div class="balckMess-bottom">
                    <div>
                      错误率：{{item.errorRate}}%
                    </div>
                    <div v-if="item.complexity  == '0601'" style='display:flex;align-items: center;'>
                      难易程度 <img style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem" src="../../assets/image/难度1.png"><img style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem" src="../../assets/image/难度2.png"><img style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem" src="../../assets/image/难度2.png">
                    </div>
                    <div  v-if="item.complexity  == '0602'" style='display:flex;align-items: center;'>
                      难易程度 <img style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem" src="../../assets/image/难度1.png"><img style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem" src="../../assets/image/难度1.png"><img style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem" src="../../assets/image/难度2.png">
                    </div>
                    <div v-if="item.complexity  == '0603'" style='display:flex;align-items: center;'>
                      难易程度 <img style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem" src="../../assets/image/难度1.png"><img style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem" src="../../assets/image/难度1.png"><img style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem" src="../../assets/image/难度1.png">
                    </div>
                  </div>
                </div>
              </div>
            </div>

          </div>
        </div>
      </div>
      <!-- 答题卡 -->
      <div class="bigBox-dtk">
        <div class='dkt-head' @click="closeDtkBox()"></div>
        <div class="dkt-message">
          <div class="dkt-message-title"></div>
          <div class="dkt-message-content"></div>
        </div>
      </div>

    </div>
    <!--底下的导航栏-->
    <div class="bottom-mes">
      <div class="bottom-mes-colect" @click="bjAdd()">
        <!-- <div class="bottom-mes-mes-0" ></div>
        <div style="color:rgba(102,102,102,1);font-size:0.28rem;margin-left:0.09rem;display:flex;align-items: center;height:0.3rem;margin-top:0.03rem;" >收藏</div> -->
      </div>
      <div class="bottom-mes-mes-dtk">
        <!-- <div class="bottom-mes-mes-dtk-1">
          <div class="bottom-mes-mes-1 " @click="bjAdd()"></div>
          <div class="bottom-mes-mes-1-1">{{bjList.length}}</div>
        </div> -->
        <div class="bottom-mes-mes-dtk-2">
          <div class="bottom-mes-mes-2"></div>
          <div class="bottom-mes-mes-2-2">{{selectList.length}}</div>
        </div>
        <div class="bottom-mes-mes-dtk-3">
          <div class="bottom-mes-mes-3"></div>
          <div class="bottom-mes-mes-3-3">{{noSelectList.length}}</div>
        </div>
        <div class="bottom-mes-mes-dtk-6">
          <div class="bottom-mes-mes-6"></div>
          <div class="bottom-mes-mes-6-6">{{noSelectNum.length}}</div>
        </div>
        <div class="bottom-mes-mes-dtk-4" @click="showDTK()">
          <div class="bottom-mes-mes-5"><span style="color: #333;">{{pageIndex}}</span>
          <span style='color:#999999'>/</span>
          <span style="color: #666;">{{allData.length}}</span></div>
        </div>
      </div>
    </div>
    <!-- 暂无内容 -->
    <div class="notData-all">
        <div class="body-mes">
            <div class="text-wait-font-mes">
                <img src="../../assets/image/新空拷贝.png" alt="" class="notData">
            </div>
            <div class="text-wait-font-mes-two">
                暂无内容
            </div>
        </div>
    </div>
    <!-- 加载页面 -->
    <Loading :dialogVisible="verDialogVisible"></Loading>
  </div>

</template>

<script>
import API from '@/api/api'
import $ from 'jquery'
import 'swiper/dist/css/swiper.css'
import Loading from './../components/loading'

import { Swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'my',
  components: {
    Swiper,
    swiperSlide,
    Loading
  },
  data () {
    return {
      isChange: 0,
      all: [
        { title: '答题模式' },
        { title: '背题模式' }
      ],
      JTclick: 0,
      id: '', // 试题id
      selfExamId: '', // 个人考试id
      pageIndex: 1, // 当前页面的下标
      bjList: [], // 表急了得题号的list
      selectList: [], // 答对了的题号
      noSelectList: [], // 答错了的题号
      swipperMes: '', // swipper的对象
      noSelectNum: [], // 未做题的数量
      allDataList: [], // 返回给接口的集合
      receiveData: [], // 试题选项的集合
      verDialogVisible: true,
      specialCode: '', // 专项类型
      practiceStatus: '', // 练习标识
      ids: [], // 查询试题的集合
      allCount: 0, // 一共多少题
      nowNum: 0, // 以获取的题号
      lastPage: 0, // 最后一次滑动请求数据的页号
      isStart: true, // 初始化swaper
      nullId: 0, // 从第几个下标插入数据
      overDataList: [], // 做过的试题集合
      overErrorDataList: [], // 做过的错误的试题集合
      isShowJx: false, // 是否显示解析
      rightAnswer: '', // 正确答案
      collectionList: [], // 收藏集合
      noCollectionList: [], // 未收藏集合
      errorItemList: [], // 做错题的集合
      backItemList: [], // 做过的集合
      wordAnswerMessage: '',
      isDone: false, // 是否做过
      isCanGo: true, // 是否可以切换模式
      isNextMessage: true, // 是否自动跳转
      isclickCollect: false, // 是否点击了收藏
      allData: '',
      testpaperItemId: '' // 获取试题的id
    }
  },
  methods: {
    // 调用接口获取数据
    getDataList () {
      const that = this
      API.post('checkTest', {
        testpaperItemId: this.testpaperItemId
      }, 'json', (data, err) => {
        if (!data.success) {
          alert(data.msg)
          return
        }
        if (data.success) {
          this.formData(data.data.exeList)
        }
      })
    },
    formData (list) {
      for (var i = 0; i < list.length; i++) {
        var rightAnswer = ''
        for (var ii = 0; ii < list[i].exeOptionList.length; ii++) {
          if (list[i].exeOptionList[ii].isTrue == 1) {
            rightAnswer += String.fromCharCode(65 + ii) + ' '
          }
        }
        if (list[i].trueOrFalse == 2) {
          this.selectList.push(i)
        }
        if (list[i].trueOrFalse == 1) {
          this.noSelectList.push(i)
        }
        if (list[i].trueOrFalse == 0) {
          this.noSelectNum.push(i)
        }
        list[i].ansString = rightAnswer
      }
      this.allData = list
      const that = this
      setTimeout(function () { that.verDialogVisible = false }, 1000)
    },
    // 初始化swiper
    showSwiper (num) {
      const that = this
      that.isCanGo = true
      const mySwiper = new Swiper('.swiper-container', {
        observer: true, // 修改swiper自己或子元素时，自动初始化swiper
        observeParents: true, // 修改swiper的父元素时，自动初始化swiper
        initialSlide: 1,
        longSwipesRatio: 0.1,
        disableOnInteraction: false,
        // allowTouchMove: false,
        on: {
          sliderMove: function () {

          }
        },
        onSlideChangeEnd: function (swiper) {
          swiper.update()

          // 　　　mySwiper.startAutoplay();
          // 　　 mySwiper.reLoop();
        }
      })

      // 主要是这部分
      mySwiper.on('slideChangeTransitionStart', function () {
        that.pageIndex = mySwiper.activeIndex + 1// 获取当前的下标
      })
      mySwiper.on('slideChangeTransitionEnd', function () {

      })
      that.swipperMes = mySwiper
      mySwiper.slideTo(Number(num), 0, false)
      that.pageIndex = num + 1
      $('.bottom-mes-mes-0').removeClass('bottom-mes-mes-1-collect')
      $('.bottom-mes-mes-0').addClass('bottom-mes-mes-1-nocollect')
    },
    // 显示答题卡
    showDTK () {
      $('.bigBox-dtk').css('display', 'block')
      $('.dkt-message').css('display', 'block')
      $('.dkt-message-title').empty()
      $('.dkt-message-content').empty()
      const that = this
      // this.$alert( "",{dangerouslyUseHTMLString: true});
      var htmlMes = ''
      for (var i = 0; i < this.allData.length; i++) {
        htmlMes += "<div class='dtk-all'><div id='dtk-" + (i + 1) + "'class='add-list-all add-list-" + (i + 1) + "'>" + (i + 1) + "</div><div style='display:none;' id='add-div-" + (i + 1) + "'></div></div>"
      }

      var titleMes = ''

      titleMes = '<div class="bottom-mes-colect" >' +
          '<div class="bottom-mes-mes-0" ></div>' +

          '</div>' +
          '<div class="bottom-mes-mes-dtk">' +
          '<div class="bottom-mes-mes-dtk-2">' +
          '<div class="bottom-mes-mes-2"></div>' +
          '<div class="bottom-mes-mes-2-2">' + this.selectList.length + '</div>' +
          '</div>' +
          '<div class="bottom-mes-mes-dtk-3">' +
          '<div class="bottom-mes-mes-3"></div>' +
          '<div class="bottom-mes-mes-3-3">' + this.noSelectList.length + '</div>' +
          '</div>' +
          '<div class="bottom-mes-mes-dtk-6">' +
          '<div class="bottom-mes-mes-6"></div>' +
          '<div class="bottom-mes-mes-6-6">' + this.noSelectNum.length + '</div>' +
          '</div>' +
          '<div class="bottom-mes-mes-dtk-4" id = "closeDtk">' +
          '<div class="bottom-mes-mes-4" ></div>' +
          '<div class="bottom-mes-mes-5">' + '<span style="color:#333;" id="now-page"></span>' + '<span style="color:#999999;">/</span>' + '<span style="color: #666;">' + this.allData.length + '</span></span>' + '</div>' +
          '</div>' +
          '</div>'
      $('.dkt-message-title').append(titleMes)
      $('.dkt-message-content').append(htmlMes)
      $('#now-page').html(this.pageIndex)
      $('.el-button').html('')
      // 定义关闭事件
      $('#closeDtk').on('click', function () {
        $('.bigBox-dtk').css('display', 'none')
        $('.dkt-message-title').empty()
        $('.dkt-message-content').empty()
        $('.dkt-message').css('display', 'none')
      })
      var isAdd = false
      // 选对样式
      for (var i = 0; i < this.selectList.length; i++) {
        if (this.pageIndex == Number(this.selectList[i] + 1)) {
          $('#dtk-' + Number(this.selectList[i] + 1)).addClass('dtk-select-now')
          isAdd = true
        } else {
          $('#dtk-' + Number(this.selectList[i] + 1)).addClass('dtk-select')
        }
      }
      // 选错样式
      for (var i = 0; i < this.noSelectList.length; i++) {
        if (this.pageIndex == Number(this.noSelectList[i] + 1)) {
          $('#dtk-' + Number(this.noSelectList[i] + 1)).addClass('dtk-bj-now')
          isAdd = true
        } else {
          $('#dtk-' + Number(this.noSelectList[i] + 1)).addClass('dtk-bj')
        }
      }
      // 添加当前题号样式
      if (!isAdd) {
        $('.add-list-' + this.pageIndex).addClass('dtk-pageIndex')
      }

      // 动态给div添加点击事件
      for (var k = 0; k < this.allData.length; k++) {
        $('#dtk-' + (k + 1)).on('click', function () {
          that.showSwiper(Number(this.innerText) - 1)
          $('.bigBox-dtk').css('display', 'none')
        })
      }
    },
    // 关闭答题卡的背景图
    closeDtkBox () {
      $('.bigBox-dtk').css('display', 'none')
      $('.dkt-message').css('display', 'none')
    },
    changeOptions () {
      for (var jj = 0; jj < this.receiveData[this.pageIndex - 1].exeOptionList.length; jj++) {
        if (this.receiveData[this.pageIndex - 1].exeOptionList[jj].isTrue == 1) {
          $('#1_' + this.receiveData[this.pageIndex - 1].exeOptionList[jj].exerciseId + '_' + String.fromCharCode(65 + jj)).removeClass(String.fromCharCode(65 + jj))
          $('#1_' + this.receiveData[this.pageIndex - 1].exeOptionList[jj].exerciseId + '_' + String.fromCharCode(65 + jj)).addClass(String.fromCharCode(65 + jj) + '-get')
          $('#1_' + String.fromCharCode(65 + jj) + '_' + this.receiveData[this.pageIndex - 1].exeOptionList[jj].exerciseId).css('color', '#04CD9D')
        }
      }
    },
    back () {
      const that = this
      // this.$router.back();
      this.$router.push({
        name: 'text_list_wait',
        params: { fromText: 1 }
      })
      //   API.post('changeCollect', {
      //     cancel : this.noCollectionList,
      //     save  : this.collectionList,
      //     specialCode : this.specialCode
      //   },'json',(data,err)=>{
      //     if(!data.success){
      //       alert(data.msg);
      //       return;
      //     }
      //     if(data.success){
      //     }
      //   })
      //   if(this.allData.length != 0){
      //     if(this.practiceStatus == 1){
      //       API.post('updateLastExe', {
      //         exeNumber : this.pageIndex,
      //         exerciseId  : this.receiveData[this.pageIndex - 1].id,
      //         specialCode : this.specialCode
      //       },'json',(data,err)=>{
      //         if(!data.success){
      //           alert(data.msg);
      //           return;
      //         }
      //         if(data.success){
      //         }
      //       })
      //     }
      //   }

      //   API.post('addBatch', {
      //     list : this.errorItemList ,
      //     specialCode : this.specialCode
      //   },'json',(data,err)=>{
      //     if(!data.success){
      //       alert(data.msg);
      //       return;
      //     }
      //     if(data.success){
      //     }
      //   })
      //   API.post('accuracyAddBatch', {
      //     list : this.backItemList
      //   },'json',(data,err)=>{
      //     if(!data.success){
      //       alert(data.msg);
      //       return;
      //     }
      //     if(data.success){
      //     }
      //   })
    },
    onBackKeyDown () {
      this.back()
    }
  },
  // 消除返回键监听
  beforeDestroy () {
    document.removeEventListener('backbutton', this.onBackKeyDown, false)
  },
  watch: {
    isclickCollect (val, oldval) {
      console.log(this.noCollectionList)
      console.log(this.collectionList)
    }
  },
  created () {
    this.testpaperItemId = this.$route.params.testpaperItemId
    this.getDataList()
  },

  mounted () {
    // 监听返回键
    document.addEventListener('backbutton', this.onBackKeyDown, false)
    $('.notData-all').hide()
    this.$nextTick(function () {
      this.showSwiper(Number(this.pageIndex) - 1)
    })
    $('.dkt-message').css('display', 'none')
  }
}
</script>

